@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;500&display=swap);
:root {
  --bg: white;
  --font: rgb(27, 27, 27);
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-weight: 300;
}

html,
body {
  height: 100%;
  background: white;
  background: var(--bg);
  color: rgb(27, 27, 27);
  color: var(--font);
}

#root {
  height: 100%;
}

footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 5vh;
}

footer p {
  background: crimson;
  color: white;
  font-weight: bold;
  padding: 0.1rem 1rem;
}

footer a {
  margin-left: 1rem;
  color: white;
  font-weight: 400;
  background-color: royalblue;
  text-decoration: none;
  padding: 0.1rem 1rem;
  margin-right: auto;
}

.footer-emoji {
  margin: 0 1em;
}

.ColorBox {
  position: relative;
  width: 20%;
  height: 25%;
  cursor: pointer;
}

.SingleColorPalette .ColorBox {
  height: 45%;
}

.ColorBox.goBack {
  background-color: black;
  color: white;
  position: absolute;
}

.see-more {
  position: absolute;
  bottom: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  font-size: 0.8rem;
  padding: 0.2rem;
}

.copy-container {
  height: 100%;
  display: grid;
  place-items: center;
}

.copy-button,
.back-button {
  opacity: 0;
  width: 100%;
  outline: none;
  border: none;
  color: white;
  background: rgba(255, 255, 255, 0.2);
  font-size: 2rem;
  padding: 0.1rem 1rem;
  cursor: pointer;
  transition: 500ms;
}

.color-name {
  position: absolute;
  top: 0;
  left: 0;
  letter-spacing: 1px;
  padding: 0.2rem;
  font-size: 0.9rem;
  color: white;
}

.ColorBox:hover .copy-button {
  opacity: 1;
}

.copy-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 100vh;
  transition: all 0.6s ease-in-out;
  z-index: 500;
}
.copy-overlay.show {
  width: 100vw;
}

.copy-overlay-msg {
  position: fixed;
  left: 0;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 4vw;
  z-index: 1000;
  transition: all 0.3s ease-in-out 0.2s;
}

.copy-overlay-txt {
  background: rgba(255, 255, 255, 0.2);
  width: 100%;
  text-align: center;
  padding: 0 5rem;
  margin: 1rem;
}

.copy-overlay-msg.show {
  left: 50%;
  transform: translate(-50%, -50%);
}

.darkText {
  color: rgb(27, 27, 27);
  font-weight: 400;
}

.darkBg {
  background: rgba(0, 0, 0, 0.3);
}

@media (max-width: 500px) {
  .copy-button {
    font-size: 1rem;
  }
}

.NavBar {
  display: flex;
  align-items: center;
  height: 8vh;
}

.DropDown {
  margin-left: auto;
}

.DropDown {
  margin-right: 1rem;
  align-self: flex-start;
}

.DropDown-nav {
  position: relative;
  padding: 0.4rem;
  width: 10rem;
  box-shadow: -2px 2px 5px #b8b8b8;
  background-color: white;
  display: flex;
  align-items: center;
  cursor: pointer;
  z-index: 3;
}

.DropDown-nav .arrow {
  background: none;
  outline: none;
  border: none;
  margin-left: auto;
  cursor: pointer;
  transform: rotate(0deg);
  transition: 0.5s;
}

.arrow svg {
  stroke: #a5a5a5;
}

.arrow.rotate {
  transform: rotate(-180deg);
}

.DropDown-menu {
  position: absolute;
  top: 35px;
  width: 10rem;
  border-radius: 0.5rem;
  background-color: white;
  overflow: hidden;
  transition: height 0.4s ease-in-out;
  z-index: 2;
}

.menu {
  width: 100%;
}

.menu-item {
  height: 50px;
  border-radius: 0.3em;
  padding: 1rem;
  display: flex;
  align-items: center;
  transition: background 300ms;
  text-decoration: none;
  color: rgb(34, 34, 34);
  cursor: pointer;
}

.menu-item:hover {
  background: #0000001a;
}

.icon-right {
  margin-left: auto;
}

.menu-enter {
  position: absolute;
  transform: translate(0, -100%);
}

.menu-enter-active {
  transform: translate(0, 0);
  transition: all 300ms ease;
}

.menu-exit {
  transform: translate(0, 0);
  position: absolute;
}

.menu-exit-active {
  transform: translateY(-100%);
  transition: all 300ms ease;
}

@media (max-width: 540px) {
  .DropDown-nav {
    width: 5rem;
    height: 1.5rem;
  }
  .DropDown-nav span {
    font-size: 0.6rem;
  }
  .DropDown-menu {
    width: 5rem;
  }
  .arrow svg {
    width: 1rem;
  }
}

.Slider-wrapper {
  display: flex;
  align-items: center;
}

.Slider {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  border-radius: 2rem;
  width: 15rem;
  height: 10px;
  background: rgb(184, 184, 184);
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

.Slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background: red;
  cursor: pointer;
}

.Slider-wrapper > p {
  font-weight: 900;
}

.Slider-wrapper .Slider-value {
  margin: 0 0.5rem;
  font-weight: 500;
}

@media (max-width: 720px) {
  .Slider {
    width: 8rem;
  }
}

@media (max-width: 500px) {
  .Slider {
    width: 5rem;
    height: 0.3rem;
  }
  .Slider::-webkit-slider-thumb {
    width: 0.8rem;
    height: 0.8rem;
  }
  .Slider-wrapper > p,
  .Slider-wrapper .Slider-value {
    font-size: 0.9rem;
  }
}

.Palette-colors {
  height: 87%;
}

